"use client";

import { useCallback, useEffect, useMemo, useState } from "react";
import { isAddress } from "ethers";

import { BackgroundScene } from "@/components/faucet/BackgroundScene";
import { CaptchaModal } from "@/components/faucet/CaptchaModal";
import { FaucetCard } from "@/components/faucet/FaucetCard";
import { LandingHero } from "@/components/faucet/LandingHero";
import { ExportedJson } from "@/lib/captchaModel";
import { zIndex } from "@/lib/designSystem";
import { generateProof, submitProofForValidation } from "@/lib/zk/prove";

type VerificationState = "unverified" | "verifying" | "verified" | "requested";
type ToastState = { type: "success" | "info"; message: string } | null;

type MintResponse = {
  ok: boolean;
  tokenId?: string | null;
  txHash?: string;
  error?: string;
};

type ClaimResponse = {
  ok: boolean;
  txHash?: string;
  cooldownRemainingSec?: number;
  error?: string;
};

export default function FaucetPage() {
  const [showFaucet, setShowFaucet] = useState(false);
  const [captchaOpen, setCaptchaOpen] = useState(false);
  const [verificationState, setVerificationState] = useState<VerificationState>("unverified");
  const [walletAddress, setWalletAddress] = useState("");
  const [exportedJson, setExportedJson] = useState<ExportedJson | null>(null);
  const [latestScore, setLatestScore] = useState<number | null>(null);
  const [copied, setCopied] = useState(false);
  const [requestLoading, setRequestLoading] = useState(false);
  const [toast, setToast] = useState<ToastState>(null);

  const isWalletValid = useMemo(() => isAddress(walletAddress.trim()), [walletAddress]);

  useEffect(() => {
    if (!toast) return;
    const timer = setTimeout(() => setToast(null), 2600);
    return () => clearTimeout(timer);
  }, [toast]);

  const handleVerifiedHuman = useCallback(async (payload: ExportedJson, score: number) => {
    setExportedJson(payload);
    setLatestScore(score);
    setCaptchaOpen(false);
    setVerificationState("verifying");
    setToast({ type: "info", message: "Human verification passed. Validating ZK proof..." });

    const rounded = Math.round(score);
    const pipelineLogs: string[] = [
      "Step 1: Received score payload from captcha.",
      `Step 2: Score normalized to integer (${rounded}).`,
      "Step 3: Generating ZK proof in browser (snarkjs + WASM)...",
    ];

    try {
      const proofResult = await generateProof(score);
      if (!proofResult.success) {
        pipelineLogs.push(`Step 4: Proof generation failed: ${proofResult.message}`);
        console.group("ZK Pipeline Logs");
        pipelineLogs.forEach((line) => console.log(line));
        console.groupEnd();
        setVerificationState("unverified");
        setToast({ type: "info", message: `ZK proof failed: ${proofResult.message}` });
        return;
      }

      pipelineLogs.push("Step 4: Proof generated successfully.");
      pipelineLogs.push("Step 5: Submitting proof for validation...");

      const { ok, verified } = await submitProofForValidation(
        proofResult.proof,
        proofResult.publicSignals,
      );
      if (!ok) {
        pipelineLogs.push("Step 6: Validation request failed.");
        console.group("ZK Pipeline Logs");
        pipelineLogs.forEach((line) => console.log(line));
        console.groupEnd();
        setVerificationState("unverified");
        setToast({ type: "info", message: "Validation request failed" });
        return;
      }

      pipelineLogs.push(`Step 6: Verification result: ${verified ? "true" : "false"}.`);

      console.group("ZK Pipeline Logs");
      pipelineLogs.forEach((line) => console.log(line));
      console.log("Proof:", proofResult.proof);
      console.log("Public signals:", proofResult.publicSignals);
      console.log("Result:", {
        verified,
        proofPath: "client-side (browser)",
        publicSignalsPath: "client-side (browser)",
      });
      console.groupEnd();

      setVerificationState(verified ? "verified" : "unverified");
      setToast(
        verified
          ? { type: "success", message: "ZK proof generated and verified." }
          : { type: "info", message: "Verification did not pass." },
      );
    } catch (err) {
      const msg = err instanceof Error ? err.message : String(err);
      pipelineLogs.push(`Error: ${msg}`);
      console.group("ZK Pipeline Logs");
      pipelineLogs.forEach((line) => console.log(line));
      console.error("Verification error:", err);
      console.groupEnd();
      setVerificationState("unverified");
      setToast({ type: "info", message: `ZK pipeline error: ${msg}` });
    }
  }, []);

  const handleRequestTokens = useCallback(async () => {
    const trimmed = walletAddress.trim();
    if (!isAddress(trimmed) || verificationState !== "verified") return;

    setRequestLoading(true);
    setVerificationState("requested");

    try {
      const mintRes = await fetch("/api/mint", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          to_address: trimmed,
          verificationPassed: true,
        }),
      });
      const mintData = (await mintRes.json()) as MintResponse;
      if (!mintRes.ok || !mintData.ok) {
        throw new Error(mintData.error ?? "Mint failed");
      }

      const claimRes = await fetch("/api/claim", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ wallet: trimmed, to: trimmed }),
      });
      const claimData = (await claimRes.json()) as ClaimResponse;
      if (!claimRes.ok || !claimData.ok) {
        throw new Error(claimData.error ?? "Claim failed");
      }
    } catch (err) {
      setVerificationState("verified");
      console.error(err);
    } finally {
      setRequestLoading(false);
    }
  }, [walletAddress, verificationState]);

  const handleCopyJson = useCallback(() => {
    if (!exportedJson) return;
    navigator.clipboard.writeText(JSON.stringify(exportedJson, null, 2));
    setCopied(true);
    setTimeout(() => setCopied(false), 2000);
  }, [exportedJson]);

  return (
    <div data-faucet className="relative z-10 min-h-[calc(100vh-56px)] flex flex-col items-center justify-center overflow-hidden">
      <BackgroundScene />
      <div className="relative z-10 flex min-h-[60vh] flex-col items-center justify-center py-12">
        {!showFaucet ? (
          <LandingHero
            onContinue={() => setShowFaucet(true)}
            animate={true}
          />
        ) : (
          <FaucetCard
            verificationState={verificationState}
            walletAddress={walletAddress}
            onWalletAddressChange={setWalletAddress}
            onOpenCaptcha={() => setCaptchaOpen(true)}
            onCopyJson={handleCopyJson}
            onRequestTokens={handleRequestTokens}
            exportedJson={exportedJson}
            copied={copied}
            requestLoading={requestLoading}
            isWalletValid={isWalletValid}
            latestScore={latestScore}
          />
        )}
      </div>
      <CaptchaModal
        isOpen={captchaOpen}
        onClose={() => setCaptchaOpen(false)}
        onVerifiedHuman={handleVerifiedHuman}
      />

      {toast && (
        <div
          className="fixed left-1/2 top-5 -translate-x-1/2 rounded-xl border border-white/15 bg-[#2a1846]/90 px-4 py-2.5 text-sm text-white shadow-xl backdrop-blur-md"
          style={{ zIndex: zIndex.toast }}
          role="status"
          aria-live="polite"
        >
          <span className={toast.type === "success" ? "text-emerald-300" : "text-purple-200"}>
            {toast.message}
          </span>
        </div>
      )}
    </div>
  );
}
